export const reducer = (
  draft: any,
  action: { type: any; payload: { id: string } }
) => {
  console.log(`reducer`);
  console.log(action);
  switch (action.type) {
    case 'toggle': {
      draft.plugins[action.payload.id].enabled = !draft.plugins[
        action.payload.id
      ].enabled;
      return;
    }

    case 'decrement': {
      draft.plugins[action.payload.id].scalingFactor =
        draft.plugins[action.payload.id].scalingFactor - 1;
      return;
    }

    case 'increment': {
      draft.plugins[action.payload.id].scalingFactor =
        draft.plugins[action.payload.id].scalingFactor + 1;
      return;
    }

    case 'enable': {
      draft.plugins[action.payload.id].enabled = true;
      return;
    }

    case 'disable': {
      draft.plugins[action.payload.id].enabled = false;
      return;
    }

    default:
      return draft;
  }
};
