import { watchImmerActions } from '@/sagas';
import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';

// import Widget from 'components/widget';

// import { reducerInitializedStore } from '@/state';
import {
  PluginList,
  defaultState,
  BasePlugin,
  State
} from 'components/base-plugin';
import { createStore, applyMiddleware } from 'redux';
import createSagaMiddleware from 'redux-saga';
import {
  createActionCreators,
  createReducerFunction,
  ImmerReducer
} from 'immer-reducer';

export class PluginReducer extends ImmerReducer<State> {
  public toggle(id: string) {
    this.draftState.plugins[id].enabled = !this.draftState.plugins[id].enabled;
  }

  public enable(id: string) {
    this.draftState.plugins[id].enabled = true;
  }

  public disable(id: string) {
    this.draftState.plugins[id].enabled = false;
  }

  public increment(id: string) {
    const current = this.draftState.plugins[id].scalingFactor;
    this.draftState.plugins[id].scalingFactor = current ? current + 1 : 1;
  }

  public decrement(id: string) {
    const current = this.draftState.plugins[id].scalingFactor;
    this.draftState.plugins[id].scalingFactor = current ? current - 1 : -1;
  }

  public reset() {
    this.draftState = defaultState;
  }
}

export const PluginActions = createActionCreators(PluginReducer);
export const reducerFunction = createReducerFunction(
  PluginReducer,
  defaultState
);

const sagaMiddleware = createSagaMiddleware();

export const store = createStore(
  reducerFunction,
  applyMiddleware(sagaMiddleware)
);

sagaMiddleware.run(watchImmerActions);

import styles from 'styles/global.scss';
import config from 'config';

if (config.debug) {
  console.warn('--- DEBUG ENABLED ---');
  // document.querySelectorAll('a[class="btn btn-sm"]')[0].id = 'test';
}

// Create modal div to contain widget and append to html doc
const modal = document.createElement(`div`);
modal.setAttribute('id', config.widgetId);
modal.className = `${styles['wcasg-ada-app-container']}`;

document.getElementsByTagName('html')[0].append(modal);

ReactDOM.render(
  <Provider store={store}>
    <PluginList
      plugins={[
        defaultState.plugins['plugin-font-size'],
        defaultState.plugins['plugin-text-spacing']
      ]}
    />
    <BasePlugin
      key={defaultState.plugins['plugin-test'].id}
      data={defaultState.plugins['plugin-test']}
    />
  </Provider>,
  modal
);
